






























































































import {Vue} from 'vue-property-decorator';
import {Loading} from 'quasar';
import cfg from "../../heating-config";
import axios from 'axios';
import moment from "moment";

require('url-search-params-polyfill');

export default Vue.extend({
  data() {
    return {
      month: null,
      sunRiseTime : null,
      sunSetTime : null,
      serverPagination: {
        page: 1,
        rowsNumber: 50 // specifying this determines pagination is server-side
      },
      monthsOptions: [
        {
          label: 'Január',
          value: '1'
        },
        {
          label: 'Február',
          value: '2'
        },
        {
          label: 'Marec',
          value: '3'
        },
        {
          label: 'Apríl',
          value: '4'
        },
        {
          label: 'Máj',
          value: '5'
        },
        {
          label: 'Jún',
          value: '6'
        },
        {
          label: 'Júl',
          value: '7'
        },
        {
          label: 'August',
          value: '8'
        },
        {
          label: 'September',
          value: '9'
        },
        {
          label: 'Október',
          value: '10'
        },
        {
          label: 'November',
          value: '11'
        },
        {
          label: 'December',
          value: '12'
        }
      ],
      schedule:{},
      tableData: [],
      inputPins: [],
      pagination: {
        sortBy: null, // String, column "name" property value
        descending: false,
        page: 1,
        rowsPerPage: 10 // current rows per page being displayed
      },
      rowSelected: [],
      columns: [{
        name: 'hour',
        required: true,
        label: 'Hodina',
        align: 'left',
        field: 'hour',
        sortable: true,
      }, {
        name: 'minute',
        required: true,
        label: 'Minúta',
        align: 'left',
        field: 'minute',
        sortable: true,
      }, {
        name: 'hor',
        required: true,
        label: 'Horizontálne',
        align: 'left',
        field: 'hor',
        sortable: true,
      }, {
        name: 'vert',
        required: true,
        label: 'Vertikálne',
        align: 'left',
        field: 'vert',
        sortable: true,
      }]
    }
  },
  methods: {
    loadCurrentState() {
      Loading.show();
      let url = cfg.BASE_URL + "solar?month=" + this.month;
      axios.get(url)
          .then(response => {
            this.tableData = response.data.positions;
            for (let i = 0; i<this.tableData.length;i++){
              this.tableData[i].id = this.tableData[i].hour +":" +this.tableData[i].minute;
            }
            this.schedule = response.data;
            this.sunRiseTime = moment(response.data.sunRiseHour+':'+response.data.sunRiseMinute,'HH:mm');
            this.sunSetTime = moment(response.data.sunSetHour+':'+response.data.sunSetMinute,'HH:mm');
            Loading.hide();
          })
          .catch(error => {
            Loading.hide();
            console.log(error)
          });
    },
    editSolar(row: any) {
      let url = cfg.BASE_URL + "solar/cmd/update?month=" + this.month;
      Loading.show();
      axios.put(url, row, {method: "PUT"})
          .then(response => {
            this.loadCurrentState();
            Loading.hide();
          })
          .catch(error => {
            Loading.hide();
            console.log(error)
          });
    },
    deleteSolar() {
      let url = cfg.BASE_URL + "solar/cmd/delete?month=" + this.month;
      Loading.show();
      axios.post(url, this.rowSelected, {method: "POST"})
          .then(response => {
            this.loadCurrentState();
            Loading.hide();
          })
          .catch(error => {
            Loading.hide();
            console.log(error)
          });
    }
  },
  mounted(): void {
    this.month = (moment().month()+1).toString();
    this.loadCurrentState();
  }
});
